.main-body {
    background: url(/static/media/background.40edc019.jpg) no-repeat;
    background-size: cover;
    background-position: center top;
    overflow: hidden;
}
.container-fluid {
    padding-left: 0 !important;
    padding-right: 0 !important;
}
body{
    font-family: 'HelveticaNeueCyr Roman', cursive;
    font-size:16px;
    color:#fff;
} 

.navbar-light .navbar-nav .nav-link {
    color: rgba(255,255, 255,1)  !important;
}
.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover {
    /* color: rgba(255,255, 255,1)  !important; */
    text-decoration: underline;
}
ul.header-links li {
    list-style-type: none;
    float: left;
    margin-right: 10px;
}
nav.navbar.navbar-expand-lg.navbar-light {
    margin-top: 20px !important;
}
.navbar-collapse button.btn.btn-outline-light {
    width: 120px;
}
.navbar-collapse button.btn.btn-outline-light img {
    padding-right: 10px;
}
.ml-auto.navbar-nav {
    -webkit-align-items: inherit;
            align-items: inherit;
}

h1, h2, h4, h5 {
    font-family: 'Avenir Next Cyr Medium'  !important;
}
h1 {
    font-size: 5rem;
    line-height: 1;
    padding-top: 50px;
}
h1 span {
    color:#FE2AFE;
}
h3.thin-heading {
    font-size: 20px;
    font-weight: 300;
    font-family: 'HelveticaNeueCyr Light';
    padding-top: 20px;
}
section.our-mission-section {
    margin-top: 46rem;
    text-align: left;
}

section.our-mission-section h4 {
    font-size: 20px;
    padding-bottom: 25px;
}
section.our-mission-section h2 {
    font-size: 1.8rem;
    line-height: 1.5;
}


section.our-mission-section .quote {
    padding-top: 5.5rem;
}

section.our-mission-section p {
    color: rgba(255,255,255, 0.75);
    font-size: 0.9rem;
    margin-top: 31px;
}
section.our-mission-section img {
    margin-top: -30px;
}
.distribution-section span {
    width: 10px;
    height: 10px;
    background: #3FD27A;
    border-radius: 50%;
    display: inline-block;
    margin-right: 8px;
}
.distribution-section button.btn.btn-outline-light {
    padding: 5px 25px;
    border-radius: 12px;
    border: 1px solid #9c4d83;
    margin-right: 30px;
    text-transform: uppercase;
}
.distribution-section h2 {
    margin-bottom: 50px;
}
.tx-box {
    background: #fff;
    padding: 10px 20px 0;
    color: #000 !important;
    border-radius: 20px;
    text-align: left !important;
    position: relative; 
}

.box-text {
    font-family: 'HelveticaNeueCyr Bold';
}
.box-number {
    font-family: 'HelveticaNeueCyr Black';
    font-size: 2.5rem;
}
.box-label {
    position: absolute;
    right: 10px;
    bottom: 18px;
    background: #3FD27F;
    padding: 3px 10px;
    border-radius: 6px;
    color: #fff;
    font-family: 'HelveticaNeueCyr Bold';
    font-size: 12px;
}

.box-label.red{
    background:#D12F4D;
}


.timer {
    font-size: 10rem;
    color: red;
    text-shadow: 0px 0 20px lightsteelblue;
    margin-top: -85rem;
}
.auto-img {
    margin-top: 100px;
}
.join-text {
    color: #fff;
    font-size: 5rem;
    line-height: 6rem;
    margin-top: 14rem;
    margin-bottom: 6rem;
}

input.subscribe-email {
    width: 43.5rem;
    height: 5rem;
    border-radius: 10px;
    padding-left: 1.5rem;
    font-size: 1.5rem;
    padding-top: 8px;
}
button.btn-primary {
    /* width: 10rem;
    height: 4rem; */
 
    background: #3108ED;
}
button.btn.btn-primary {
    border: none;
    line-height: 22px;
    padding: 12px 36px;
    border-radius: 11px;
    font-size: 16px;
    margin-top: 30px;
}
button.btn-primary:hover {
    background:#fff;
    color: #3108ED;
}
.footer .container {
    text-align: left;
    border-top: 1px solid #9c4d83;
    margin-top: 18rem;
    padding-top: 30px;
}
.footer ul li {
    list-style-type: none;

}
.footer ul li a {
    color: #fff !important;
    text-align: left;
    padding-bottom: 20px;
    display: block;
}
.right-footer .footer-links {
    margin-top: 80px;
    text-align: right;
    margin-left: 84px;
}
.footer-copyright {
    margin-top: 70px;
    font-size: 14px;
    color:#c1a3c3;
}
.right-footer .footer-links li {
    float:left;
    margin-left:15px;
}
.recent-minted {
    margin-top: 150px;
}
.recent-minted h4 {
    text-align: center;
    margin-bottom: 30px;
}
.btn-outline-light{
    background:transparent;
    border: 1px solid #fff;
}
.btn-outline-light:hover {
    color: #fff;
    background-color: #3108ED;
    border-color: #f8f9fa;
    border:1px solid #3108ED;
}
section.distribution-section {
    margin-top: 50px;
    margin-bottom: 50px !important;
}
.right-footer button.btn.btn-outline-light img {
    margin-right: 10px;
}
.social-links {
    display: block;
    margin: 10rem auto;
}
.social-links li {
    padding-right: 5rem;
    display: inline-block;
}

.right-footer {
    text-align: right;
}
.carousel-container {
    width: 100%;
   }

.navbar-light .navbar-toggler-icon {
    background-image: url(/static/media/menu.8f5f5f36.svg) !important;
}
@media (min-width: 1601px){
    h1 {
        font-size:8rem;
    }
    h3.thin-heading {
        font-size: 24px;
    }
    section.our-mission-section h4 {
        font-size: 24px;
        padding-bottom: 25px;
    }
    section.our-mission-section p {
        color: rgba(255,255,255, 0.75);
        font-size: 1rem;
        margin-top: 31px;
    }
    .recent-minted {
        margin-top: 13rem;
        margin-bottom: 12rem;
    }
}

@media (max-width:1600px) {
    .timer{
        margin-top: -74rem;
    }
}


@media (max-width:1500px) {
    .timer{
        margin-top: -64rem;
    }
}

@media (max-width:1200px) {
    .timer{
        margin-top: -54rem;
        font-size:8rem;
    }
}


@media (max-width:1040px) {
    .timer{
        margin-top: -44rem;
        font-size: 6rem;
    }
}


@media (max-width:991px) {
    .timer{
        margin-top: -34rem;
        font-size: 4rem;
    }
    .join-text {
        font-size:3rem;
    }
    input.subscribe-email {
        width:37rem;
        height:4rem;
    }
    .tx-box {
        margin-bottom: 20px;
    }
    section.our-mission-section {
        margin-top: 70rem;
        text-align: left;
    }
    section.our-mission-section img {
        margin-top: -30px;
        text-align: center;
        margin-left: -50px;
    }
    h1 {
        font-size: 4rem;
        line-height: 1;
        padding-top: 250px;
    }
    .footer .container {
        text-align: center;
        border-top: 1px solid #9c4d83;
        margin-top: 38rem;
        padding-top: 30px;
    }
    .footer-copyright {
        margin-top: 70px;
        font-size: 14px;
        color: #c1a3c3;
        margin-bottom: 50px;
    }
    .footer ul li a {
        color: #fff !important;
        text-align: center;
        padding-bottom: 20px;
        display: block;
    }
    .right-footer {
        text-align: center;
    }
}

@media (max-width:700px) {
    .timer{
        margin-top: -26rem;
        font-size: 3rem;
    }
    .social-links li {
        padding-right: 2rem;

    }
    .social-links li img {
        width: 60px;
    }
}

@media(max-width: 600px){
    .timer {
        margin-top: -21rem;
        font-size: 3rem;
    }
    .join-text {
        font-size: 2rem;
        line-height: 3rem;
    }
    input.subscribe-email {
        width: 18rem;
        height: 4rem;
        font-size:1rem;

    }
    button.btn-primary {
        width: 16rem;
        height:3.5rem;
        font-size:1rem;
    }
    .social-links li img {
        width: 50px;
    }

}

@media(max-width:450px) {
    .timer {
        margin-top: -18.5rem;
        font-size: 2rem;
    }
    input.subscribe-email {
        width: 17rem;
        height: 3.5rem;
    }
}


@media(max-width:370px) {
    .timer {
        margin-top: -15.5rem;
        font-size: 2rem;
    }
}

